import { AsyncSubject, catchError, EMPTY, filter, forkJoin, map, mapTo, of, ReplaySubject, Subject, switchMap, takeUntil, tap } from 'rxjs';
import { PeerjsSession } from './peerjs';
import { QueryResponseMapping } from './player/remote-player';
import { playerAck } from './player';
export class RemoteGame {
    constructor(modal, player) {
        this.modal = modal;
        this.player = player;
        this.gameCode$ = new ReplaySubject(1);
        this.playerIndex = 1;
    }
    run() {
        return this.modal.getGameCode().pipe(switchMap(gameCode => {
            this.gameCode$.next(gameCode);
            return forkJoin([PeerjsSession.getHostKey(gameCode, this.playerIndex), PeerjsSession.getGuestKey(gameCode, this.playerIndex)]);
        }), switchMap(([hostKey, guestKey]) => this.runGame(new PeerjsSession(guestKey, hostKey))), mapTo(null));
    }
    runGame(peer) {
        const done = new AsyncSubject();
        const endEvent$ = new AsyncSubject();
        const endSelectCards$ = new Subject();
        const players = new Subject();
        peer.events$
            .pipe(catchError(() => EMPTY), takeUntil(endEvent$))
            .subscribe({
            next: event => {
                let response;
                switch (event.type) {
                    case 'END':
                        endEvent$.next();
                        endEvent$.complete();
                        break;
                    case 'GAME_INIT':
                        response = this.makeEvent(this.gameCode$.pipe(switchMap(gameCode => this.player.init(Object.assign(Object.assign({}, event), { players, gameCode }))), tap(resp => {
                            if (resp.hasOwnProperty('leave')) {
                                peer.close();
                            }
                        }), filter(event => event.hasOwnProperty('name'))), 'NAME', event.uuid);
                        break;
                    case 'PLAYER_NAME':
                        players.next(event);
                        response = this.makeEvent(of(playerAck()), 'ACK', event.uuid);
                        break;
                    case 'ROUND_START':
                        response = this.answerTo(event, (ev) => this.player.startRound(ev));
                        break;
                    case 'ACTIVE_PLAYER':
                        response = this.answerTo(event, (ev) => this.player.activePlayer(ev));
                        break;
                    case 'SELECT_CARDS':
                        response = this.answerTo(event, () => this.player.selectCards().pipe(takeUntil(endSelectCards$)));
                        break;
                    case 'END_SELECT_CARDS':
                        endSelectCards$.next();
                        response = this.makeEvent(of(playerAck()), 'ACK', event.uuid);
                        break;
                    case 'CARD_REVEALED':
                        response = this.answerTo(event, (ev) => this.player.cardRevealed(ev));
                        break;
                    case 'CARDS_HIDDEN':
                        response = this.answerTo(event, () => this.player.cardsHidden());
                        break;
                    case 'PAIR_SOLVED':
                        response = this.answerTo(event, (ev) => this.player.cardsSolved(ev));
                        break;
                    case 'ROUND_END':
                        response = this.answerTo(event, (ev) => this.player.endRound(ev));
                        break;
                    case 'PLAYER_LEFT':
                        response = this.answerTo(event, (ev) => this.player.playerLeft(ev));
                        break;
                }
                if (response) {
                    response.subscribe(answer => peer.send(answer));
                }
            },
            complete: () => {
                peer.close();
                done.next();
                done.complete();
            }
        });
        return done.asObservable();
    }
    answerTo(event, getAnswer) {
        return this.makeEvent(getAnswer(event), QueryResponseMapping[event.type], event.uuid);
    }
    makeEvent(event$, type, uuid) {
        return event$.pipe(map(event => (Object.assign(Object.assign({}, event), { type,
            uuid }))));
    }
}
