import axios from 'axios';
import '../utils/utils';
import { from, map } from 'rxjs';
import { shuffleArray } from './shuffle';
export class DogApi {
    constructor(baseURL) {
        this.baseURL = baseURL;
        this.filter = 'webm,mp4';
        this.api = axios.create({ baseURL: this.baseURL });
    }
    getDogURL() {
        return this.api
            .get(`/woof?filter=${this.filter}`)
            .then((response) => `${this.baseURL}${response.data}`);
    }
    getDogs(howMany) {
        return from(this.api.get(`/doggos?filter=${this.filter}`)).pipe(map(response => response.data), map(doggos => shuffleArray(doggos).slice(0, howMany)), map(doggos => doggos.map(url => `${this.baseURL}${url}`)));
    }
    downloadDog(url) {
        return this.api
            .get(url, { responseType: 'blob' })
            .then((imgBlob) => URL.createObjectURL(imgBlob.data));
    }
}
