import { AsyncSubject, map } from 'rxjs';
import { modalQueue } from './modal';
export function getPlayerName(message, button) {
    return queryPlayer(message, null, button, true).pipe(map(answer => answer.input));
}
export function queryPlayer(title = '', message = null, button = '', input = true, inputValidation) {
    const result = new AsyncSubject();
    const buttons = (Array.isArray(button) ? button : [button]).map((buttonLabel, index) => ({
        label: buttonLabel,
        action: (answer) => {
            result.next({ input: answer, buttonClicked: index });
            result.complete();
        }
    }));
    modalQueue.next({
        title,
        message,
        input,
        inputValidation,
        buttons
    });
    return result.asObservable();
}
