import { AsyncSubject, filter, map, mapTo, merge, of, ReplaySubject, tap } from 'rxjs';
import { playerAck, playerLeave, playerNewRound } from './';
export class LocalPlayer {
    constructor(board, header, modal) {
        this.board = board;
        this.header = header;
        this.modal = modal;
        this.playerLeft$ = new AsyncSubject();
        this.playerIndex$ = new ReplaySubject(1);
    }
    end() { }
    cardRevealed(revealed) {
        this.board.revealCard(revealed);
        return of(playerAck());
    }
    cardsHidden() {
        this.board.hideCards();
        return of(playerAck());
    }
    cardsSolved(event) {
        this.board.pairSolved(event);
        return of(playerAck());
    }
    endRound(event) {
        return this.modal.getNewRound(event).pipe(tap(newRound => {
            if (!newRound) {
                this.playerLeft$.next();
                this.playerLeft$.complete();
            }
        }), filter(newRound => newRound), mapTo(playerNewRound()));
    }
    init(event) {
        this.header.setNumberOfPlayers(event.numPlayers);
        this.header.setGameCode(event.gameCode);
        event.players.subscribe(player => this.header.setPlayerName(player.name, player.index));
        this.playerIndex$.next(event.playerIndex);
        return merge(this.playerLeft$.pipe(mapTo(playerLeave())), this.modal.getName(event.playerIndex)
            .pipe(map(name => ({ name }))));
    }
    selectCards() {
        return this.board.getCardSelection().pipe();
    }
    startRound(event) {
        return this.board.setup(event).pipe(mapTo(playerAck()));
    }
    activePlayer(event) {
        this.header.setActivePlayer(event.playerIndex);
        return of(playerAck());
    }
    playerLeft(event) {
        return of(playerAck());
    }
}
