import { __rest } from "tslib";
import { v4 as uuidv4 } from 'uuid';
import { playerLeave } from './index';
import { Observable, catchError, EMPTY, filter, first, ReplaySubject, Subject, switchMap, take, tap } from 'rxjs';
import { PeerjsSession } from '../peerjs';
export const QueryResponseMapping = {
    CARDS_HIDDEN: 'ACK',
    CARD_REVEALED: 'ACK',
    GAME_INIT: 'NAME',
    PAIR_SOLVED: 'ACK',
    PLAYER_LEFT: 'ACK',
    ROUND_END: 'NEW_ROUND',
    ROUND_START: 'ACK',
    ACTIVE_PLAYER: 'ACK',
    SELECT_CARDS: 'CARD_SELECTED',
    END_SELECT_CARDS: 'ACK',
    PLAYER_NAME: 'ACK',
    END: 'ACK' // not really though
};
export class RemotePlayer {
    constructor() {
        this.session$ = new ReplaySubject(1);
    }
    end() {
        this.session$.pipe(take(1)).subscribe(session => {
            session.send(this.makeEvent({}, 'END'));
        });
    }
    activePlayer(event) {
        return this.query(this.makeEvent(event, 'ACTIVE_PLAYER'), true);
    }
    cardRevealed(revealed) {
        return this.query(this.makeEvent(revealed, 'CARD_REVEALED'), true);
    }
    cardsHidden() {
        return this.query(this.makeEvent({}, 'CARDS_HIDDEN'), true);
    }
    cardsSolved(event) {
        return this.query(this.makeEvent(event, 'PAIR_SOLVED'), true);
    }
    endRound(event) {
        return this.query(this.makeEvent(event, 'ROUND_END'), true);
    }
    init(event) {
        const response$ = new Subject();
        const { players, gameCode } = event, rawEvent = __rest(event, ["players", "gameCode"]);
        PeerjsSession.getHostKey(event.gameCode, event.playerIndex).pipe(switchMap(hostKey => {
            this.session$.next(new PeerjsSession(hostKey));
            return this.session$;
        })).subscribe(session => {
            session.events$.subscribe({
                error: () => {
                    response$.next(playerLeave());
                    response$.complete();
                }
            });
            this.query(this.makeEvent(rawEvent, 'GAME_INIT'), true)
                .pipe(tap(() => {
                players.subscribe(player => {
                    this.query(this.makeEvent(player, 'PLAYER_NAME'), true).subscribe();
                });
            }))
                .subscribe(response$);
        });
        return response$;
    }
    playerLeft(event) {
        return this.query(this.makeEvent(event, 'PLAYER_LEFT'), true).pipe(tap(() => this.session$.subscribe(() => {
            session => session.close();
            this.session$.complete();
        })));
    }
    selectCards() {
        const selected$ = this.query(this.makeEvent({}, 'SELECT_CARDS'));
        return new Observable(subscriber => {
            selected$.subscribe(subscriber);
            return () => {
                this.query(this.makeEvent({}, 'END_SELECT_CARDS'), true).subscribe();
            };
        });
    }
    startRound(event) {
        return this.query(this.makeEvent(event, 'ROUND_START'), true);
    }
    query(query, expectOne) {
        return this.session$.pipe(take(1), switchMap(session => {
            const answer$ = session.events$.pipe(catchError(() => EMPTY), filter(event => event.uuid === query.uuid), expectOne ? first() : (obs) => obs);
            session.send(query);
            return answer$;
        }));
    }
    makeEvent(query, type) {
        return Object.assign(Object.assign({}, query), { type, uuid: uuidv4() });
    }
}
